import ttStatusBar from '../../components/tt-status-bar/tt-status-bar.vue'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import ttImage from '@/components/tt-image/tt-image.vue'
export default {
	components: {ttStatusBar,uniNavBar,ttImage},
	data() {
		return {
			id: '',
			userid:'',
			text:'',
			image: '',
			uploadFile:'',
			statusBarHeight: ''
		}
	},
	created() {
		this.getSystemInfo()
	},
	methods: {
		getSystemInfo(){
			uni.getSystemInfo({
			    success: (res)=> {
					this.statusBarHeight = res.statusBarHeight - 1 + 'px'
			    }
			});
		},
		clickLeft(){
			uni.navigateBack()
		},
		chooseImage(){
			this.$refs.addImage.chooseImage()
		},
		getImgList(data){
			this.image = data[0]
			let url




			url = `${this.GLOBAL.baseURL}/upload`

			uni.uploadFile({
				url: url,
				filePath: data[0],
				name: 'file',
				formData: {},
				success: (res) => {
					this.uploadFile = res.data
				}
			});
			
		},
		submit(){
			let id = this.id
			let userid = this.userid
			let text = this.text
			let pic = this.uploadFile
			console.log(pic)
			let data = {
				id: id,
				text: text,
				pic: pic
			}
			let url




			url = `${this.GLOBAL.baseURL}/m/essay_submit`

			uni.request({
			    url: url,
				method: 'POST',
			    data: data,
			    success: (res) => {
					if(res.statusCode===200){
						uni.showToast({
						    title: res.data.msg,
							icon: 'none',
							position: 'bottom',
						    duration: 600
						});
						setTimeout(() => {
							uni.switchTab({
								url: "../../pagesA/community/community",
								success(){
								}
							});
						}, 600);
					}
			    },
				
			});
		}
	}
}
